@use "base/variables" as v;
@use "base/mixins" as m;

.listado-servicios {
    padding: 1rem;
    display: grid;
    gap: 2rem;
    text-align: center;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .servicio {
        width: 100%;
        border-radius: 0.5rem;
        background-color: v.$blanco;
        transition: background-color 0.3s ease-in-out, transform 0.5s ease-in-out;

        .nombre-servicio {
            margin: 1rem 0;
            color: v.$negro;
            transition: color 0.3s ease-in-out;
        }

        .precio-servicio {
            margin: 0 0 1rem 0;
            font-size: 3rem;
            color: v.$azul;
            font-weight: v.$bold;
            transition: color 0.3s ease-in-out;
        }

        &:hover {
            transform: scale(1.1);
            cursor: pointer;
            background-color: v.$azul;

            .nombre-servicio {
                color: v.$blanco;
            }

            .precio-servicio {
                color: v.$blanco;
            }
        }

        &.seleccionado {
            background-color: v.$azul;

            .nombre-servicio {
                color: v.$blanco;
            }

            .precio-servicio {
                color: v.$blanco;
            }
        }
    }
}
