@use "variables" as v;
@use "mixins" as m;

::-webkit-scrollbar {
    width: 1.5rem;
}
::-webkit-scrollbar-track {
    background: lighten(v.$azul, 20);
}
::-webkit-scrollbar-thumb {
    background-color: v.$azul;
    border: 1rem;
    transition: all 0.3s ease-in;

    &:hover {
        background-color: darken(v.$azul, 8%);
    }
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100vh;
}
body {
    display: flex;
    flex-direction: column;
    background-color: v.$negro;
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.text-center {
    text-align: center;
}
