}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100vh;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.text-center {
  text-align: center;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.5rem;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.6rem;
  }
}

h3 {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/barberia.jpg);
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: 100vh;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 2rem;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    height: 100vh;
    overflow-y: scroll;
  }
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra span {
  color: #0f72a3;
  font-weight: 700;
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    margin-bottom: 2rem;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
  padding: 1rem 2rem;
}

.seccion {
  padding: 4rem 0 0 0;
  display: none;
}
@media (min-width: 768px) {
  .seccion {
    padding: 4rem 0;
  }
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  padding: 1rem;
  display: grid;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  width: 100%;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  transition: background-color 0.3s ease-in-out, transform 0.5s ease-in-out;
}
.listado-servicios .servicio .nombre-servicio {
  margin: 1rem 0;
  color: #1a1b15;
  transition: color 0.3s ease-in-out;
}
.listado-servicios .servicio .precio-servicio {
  margin: 0 0 1rem 0;
  font-size: 3rem;
  color: #0f72a3;
  font-weight: 700;
  transition: color 0.3s ease-in-out;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
  cursor: pointer;
  background-color: #0f72a3;
}
.listado-servicios .servicio:hover .nombre-servicio {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover .precio-servicio {
  color: #FFFFFF;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0f72a3;
}
.listado-servicios .servicio.seleccionado .nombre-servicio {
  color: #FFFFFF;
}
.listado-servicios .servicio.seleccionado .precio-servicio {
  color: #FFFFFF;
}

.contenido-resumen {
  padding: 2rem 0;
}
.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0f72a3;
  font-weight: 700;
  font-size: 2rem;
}
.contenido-resumen .precio {
  text-align: right;
  font-weight: 700;
  font-size: 3rem;
}

.contenedor-servicio {
  border-bottom: 0.1rem solid #e1e1e1;
  padding: 1rem 0;
}
.contenedor-servicio p {
  margin: 1rem 0;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 2rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
  transition: background-color 0.3s ease-in;
}
.citas li, .servicios li {
  border-top: 0.1rem solid #FFFFFF;
  overflow: hidden;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0f72a3;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li .total, .servicios li .total {
  margin: 0;
  font-size: 3rem;
  text-align: right;
}
.citas li .total span, .servicios li .total span {
  font-weight: 700;
  font-size: 3.2rem;
}

.servicios .acciones {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .servicios .acciones {
    flex-direction: row;
  }
}
.servicios .acciones .boton {
  margin: 0;
  color: #FFFFFF;
  padding: 1rem 2rem;
}
.servicios .acciones .boton.eliminar {
  background-color: #cb0000;
}
.servicios .acciones .boton.eliminar:hover {
  background-color: #980000;
}

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .campo {
    flex-direction: row;
  }
}
.campo label {
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .campo label {
    flex: 0 0 15rem;
  }
}
.campo input {
  flex: 1;
  padding: 1rem;
  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 0.1rem solid #0f72a3;
  border-radius: 0.5rem;
  color: #FFFFFF;
}
.campo input:disabled {
  background-color: #6d7158;
  border: none;
  cursor: not-allowed;
}
.campo .boton-eliminar {
  border: 0.1rem solid #cb0000;
  transition: background-color 0.5s ease-in-out;
  font-weight: 700;
  margin: 2rem 2rem 0 2rem;
}
.campo .boton-eliminar:hover {
  background-color: #cb0000;
  cursor: pointer;
}

.boton {
  background-color: #0f72a3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0b5174;
  cursor: pointer;
}

.acciones {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
  margin: 2rem 0;
}
.acciones a {
  color: #0f72a3;
}
.acciones a:hover {
  color: #0d628c;
}

.alerta {
  padding: 1rem 0;
  text-align: center;
  margin-bottom: 1rem;
  font-weight: 700;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0f72a3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: cursor 0.3s linear, background-color 0.3s linear;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0d628c;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0f72a3;
}

@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
}
.paginacion .boton {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}