@use "base/variables" as v;
@use "base/mixins" as m;

.campo{
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;

    @include m.tablet{
        flex-direction: row;
    }

    label{
        margin: 1rem 0;
        @include m.tablet{
            flex: 0 0 15rem;
        }
    }
    input{
        flex: 1;
        padding: 1rem;
        background-color: transparent;
        outline: none;
        border: none;
        border-bottom: .1rem solid v.$azul;
        border-radius: .5rem;
        color: v.$blanco;

        &:disabled{
            background-color: lighten(v.$negro, 30%);
            border: none;
            cursor: not-allowed;
        }
    }

    .boton-eliminar{
        border: .1rem solid v.$rojo;
        transition: background-color .5s ease-in-out;
        font-weight: v.$bold;
        margin: 2rem 2rem 0 2rem;

        &:hover{
            background-color: v.$rojo;
            cursor: pointer;
        }
    }
}