@use "base/variables" as v;
@use "base/mixins" as m;

.citas {
    list-style: none;
    padding: 0;
    transition: background-color 0.3s ease-in;
    li {
        border-top: 0.1rem solid v.$blanco;
        overflow: hidden;
        p {
            font-weight: v.$bold;
            color: v.$azul;
        }

        span,
        .servicio {
            font-weight: v.$regular;
            color: v.$blanco;
        }

        .total {
            margin: 0;
            font-size: 3rem;
            text-align: right;

            span {
                font-weight: v.$bold;
                font-size: 3.2rem;
            }
        }
    }
}

.servicios{
    @extend.citas;

    .acciones{
        display: flex;
        
        @include m.tablet{
            flex-direction: row;
        }
        justify-content: space-between;
        .boton{
            margin: 0;
            color: v.$blanco;
            padding: 1rem 2rem;

            &.eliminar{
                background-color: v.$rojo;

                &:hover{
                    background-color: darken( v.$rojo, 10% );
                }
            }
        }
    }
}
