@use "base/variables" as v;

.contenido-resumen {
    padding: 2rem 0;

    p {
        margin-bottom: 1rem;
    }

    span {
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2rem;
    }

    .precio {
        text-align: right;
        font-weight: v.$bold;
        font-size: 3rem;
    }
}

.contenedor-servicio {
    border-bottom: 0.1rem solid v.$gris;
    padding: 1rem 0;

    p {
        margin: 1rem 0;
    }

    &:last-of-type {
        margin-bottom: 2rem;
    }
}
