@use "variables" as v;
@use "mixins" as m;

h1,
h2,
h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 3rem;
    @include m.tablet {
        font-size: 4rem;
    }
}
h2 {
    font-size: 2.5rem;
    margin: 2rem 0;

    @include m.tablet {
        font-size: 3.6rem;
    }
}
h3 {
    font-size: 2rem;
    text-align: center;

    @include m.tablet {
        font-size: 3rem;
    }
}
p {
    color: v.$blanco;
    font-size: 2rem;
}

.nombre-pagina {
    text-align: center;
    font-weight: v.$bold;
}

.descripcion-pagina {
    text-align: center;
}
