@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/barberia.jpg);
        background-position: center center;
        background-size: cover;

        @include m.tablet {
            height: 100vh;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 2rem;

        @include m.tablet {
            height: 100vh;
            overflow-y: scroll;
        }
    }
}

.barra {
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    span {
        color: v.$azul;
        font-weight: v.$bold;
    }
    .boton {
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
    }
}

.barra-servicios {
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        gap: 2rem;
        margin-bottom: 2rem;
    }

    .boton{
        margin: 0 0 1rem 0;
        padding: 1rem 2rem;
    }
}
